var _this;
export default {
	data() {
		return {
			collegeId: '',
			courseId: '',
			myData: Object,
			baseUrl: "http://39.105.48.243:8080/crlink/",
		}
	},
	onLoad(e) {
		_this = this;
		_this.collegeId = e.collegeId
		_this.courseId = e.courseId

		if (_this.collegeId) {
			_this.getCollegeMes()
		}
		if (_this.courseId) {
			_this.getCourseMes()
		}

	},
	methods: {
		getCollegeMes() {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('college!ajaxGetCollegeDetailNew.action', {
				collegeId: _this.collegeId,
				loginKey: loginkey,
			}).then(res => {
				if (res.res.status == 0) {
					_this.myData = res.inf;
				} else {
					uni.showToast({
						title: res.res.error
					})
				}
			})
		},
		getCourseMes() {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('index!ajaxGetCourseDetailNew.action', {
				id: _this.courseId,
				loginKey: loginkey,
			}).then(res => {
				if (res.res.status == 0) {
					_this.myData = res.inf;
				} else {
					uni.showToast({
						title: res.res.error
					})
				}
			})
		},
		topay() {

			if (_this.courseId) {
				console.log("danke");
				//单课支付
				var loginkey = uni.getStorageSync('loginKey');

				uni.request({
					url: 'http://39.105.48.243:8080/crlink/pay/wxPay!ajaxBuyCourse.action',
					data: {
						id: _this.courseId,
						loginKey: loginkey,
						tradeType:'JSAPI'
					},
					success: (res) => {
						console.log(res.data);
						_this.wxpay(res.data.inf)
					}
				})
			} else {
				//课程支付
				console.log("kecheng");
				var loginkey = uni.getStorageSync('loginKey');
				uni.request({
					url: 'http://39.105.48.243:8080/crlink/pay/wxPay!ajaxBuyCourseCollege.action',
					data: {
						id: _this.courseId,
						loginKey: loginkey,
						tradeType:'JSAPI' 
					},
					success: (res) => {
						_this.wxpay(res.data.inf)
					}
				})
			}
		},

		wxpay(dict) {
			
				console.log("dict timestamp= "+dict.timestamp);
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: dict.timestamp,
				nonceStr: dict.nonceStr,
				package: 'prepay_id='+dict.prepayId,
				signType: 'MD5',
				paySign: dict.paySign,
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}

	},

	computed: {
		title() {
			if (_this.collegeId) {
				return _this.myData.collegeName

			} else {
				return _this.myData.title
			}
		},

		price() {
			if (_this.collegeId) {
				return _this.myData.collegeFee

			} else {
				return _this.myData.buyPrice
			}
		}

	}

}
